import React, { useState } from "react";
import "./Notice.css"; // CSS  import

function Notice() {
  const notices = [
    { id: 45, category: "[]", title: "  !", date: "2024.12.02", author: "", views: 13148 },
    { id: 44, category: "[]", title: "   (,    )", date: "2024.11.28", author: "", views: 13696 },
    { id: 43, category: "[]", title: "      & ", date: "2024.11.20", author: "", views: 14134 },
    { id: 42, category: "[]", title: "   ", date: "2024.11.08", author: "", views: 24851 },
    { id: 41, category: "[]", title: "2024        ", date: "2024.09.12", author: "", views: 42953 },
    { id: 40, category: "[]", title: "     (8/8)", date: "2024.08.07", author: "", views: 53731 },
    { id: 39, category: "[]", title: "    ", date: "2024.08.05", author: "", views: 55154 },
    { id: 38, category: "[]", title: "   _5 22", date: "2024.05.22", author: "", views: 82901 },
    { id: 37, category: "[]", title: "   _5 7", date: "2024.05.07", author: "", views: 90279 },
  ];

  const [currentPage, setCurrentPage] = useState(1);
  const [searchQuery, setSearchQuery] = useState("");
  const itemsPerPage = 5;

  const filteredNotices = notices.filter((notice) =>
    notice.title.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentNotices = filteredNotices.slice(indexOfFirstItem, indexOfLastItem);

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const handleSearch = (event) => {
    setSearchQuery(event.target.value);
    setCurrentPage(1);
  };

  return (
    <div className="notice-container">
      <h1 className="notice-title"></h1>

      {/*    */}
      <div className="notice-search-container">
        <select className="notice-search-select">
          <option value="title"></option>
        </select>
        <input
          type="text"
          value={searchQuery}
          onChange={handleSearch}
          placeholder=" "
          className="notice-search-input"
        />
        <button
          className="notice-search-button"
          onClick={() => setCurrentPage(1)}
        >
          
        </button>
      </div>

      {/*   */}
      <table className="notice-table">
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          {currentNotices.map((notice) => (
            <tr key={notice.id}>
              <td>{notice.id}</td>
              <td>{notice.category}</td>
              <td className="title">{notice.title}</td>
              <td>{notice.date}</td>
              <td>{notice.author}</td>
            </tr>
          ))}
        </tbody>
      </table>

      {/*  */}
      <div className="notice-pagination">
        {Array.from({ length: Math.ceil(filteredNotices.length / itemsPerPage) }, (_, index) => (
          <button
            key={index + 1}
            onClick={() => handlePageChange(index + 1)}
            className={currentPage === index + 1 ? "active" : "inactive"}
          >
            {index + 1}
          </button>
        ))}
      </div>
    </div>
  );
}

export default Notice;