import Container from 'react-bootstrap/Container';
import { Nav, Navbar, NavDropdown } from 'react-bootstrap';
import React from "react";
import { Link } from "react-router-dom";
import './SecondHeader.css';

function SecondHeader(){
    return(
        <Navbar expand="lg" className="bg-body-tertiary">
            <Container>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="me-auto">
                        <Nav.Link as={Link} to="/"></Nav.Link>
                        <Nav.Link as={Link} to="/recommendedproduct"></Nav.Link>
                        <NavDropdown title="" id="basic-nav-dropdown">
                            <NavDropdown.Item as={Link} to="/fruitsproduct"></NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/vegetablesproduct">
                                
                            </NavDropdown.Item>
                            <NavDropdown.Item as={Link} to="/grainsproduct"></NavDropdown.Item>
                        </NavDropdown>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    );
}

export default SecondHeader;