import React, { useState } from 'react';
import { Card, Container, Row, Col, Pagination } from 'react-bootstrap';
import './ProductPage.css';
import DefaultImg from "../../assets/default.jpg";

function ProductPage() {
    const [products] = useState([
        {
            id: 1,
            name: ' ',
            discount: '55%',
            price: '26,500',
            originalPrice: '59,000',
            rating: '4.9',
            reviews: ' 10',
            image: DefaultImg,
        },
        {
            id: 2,
            name: ' ',
            discount: '51%',
            price: '38,900',
            originalPrice: '79,000',
            rating: '4.8',
            reviews: ' 642',
            image: DefaultImg,
        },
        {
            id: 3,
            name: ' ',
            discount: '37%',
            price: '35,900',
            originalPrice: '57,000',
            rating: '4.8',
            reviews: ' 44,451',
            image: DefaultImg,
        },
        {
            id: 4,
            name: '[  ]  1',
            discount: '45%',
            price: '109,000',
            originalPrice: '199,000',
            rating: '4.7',
            reviews: ' 21,193',
            image: DefaultImg,
        },
        {
            id: 5,
            name: ' ',
            discount: '45%',
            price: '109,000',
            originalPrice: '199,000',
            rating: '4.7',
            reviews: ' 21,193',
            image: DefaultImg,
        },
        {
            id: 6,
            name: ' ',
            discount: '45%',
            price: '109,000',
            originalPrice: '199,000',
            rating: '4.7',
            reviews: ' 21,193',
            image: DefaultImg,
        },
        {
            id: 7,
            name: '  ',
            discount: '45%',
            price: '109,000',
            originalPrice: '199,000',
            rating: '4.7',
            reviews: ' 21,193',
            image: DefaultImg,
        },
        {
            id: 8,
            name: '  ',
            discount: '45%',
            price: '109,000',
            originalPrice: '199,000',
            rating: '4.7',
            reviews: ' 21,193',
            image: DefaultImg,
        },
    ]);

    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 8;

    const indexOfLastItem = currentPage * itemsPerPage;
    const indexOfFirstItem = indexOfLastItem - itemsPerPage;
    const currentProducts = products.slice(indexOfFirstItem, indexOfLastItem);

    const totalPages = Math.ceil(products.length / itemsPerPage);

    const handlePageChange = (pageNumber) => {
        setCurrentPage(pageNumber);
    };

    return (
        <Container>
            <h5 className="my-4"> </h5>
            <hr/>
            <Row>
                {currentProducts.map((product) => (
                    <Col md={3} key={product.id} className="mb-4">
                        <Card className="product-card">
                            <div className="card-img-container">
                                <Card.Img variant="top" src={product.image} alt={product.name} className="product-image" />
                            </div>
                            <Card.Body>
                                <Card.Title>{product.name}</Card.Title>
                                <Card.Text>
                                    <span className="discount">{product.discount}</span>
                                    <br />
                                    <span className="text-muted">{product.originalPrice}</span>{' '}
                                    <strong>{product.price}</strong>
                                    <br />
                                    <small>: {product.rating} / {product.reviews}</small>
                                </Card.Text>
                            </Card.Body>
                        </Card>
                    </Col>
                ))}
            </Row>
            <Pagination className="justify-content-center">
                {[...Array(totalPages).keys()].map((number) => (
                    <Pagination.Item
                        key={number + 1}
                        active={number + 1 === currentPage}
                        onClick={() => handlePageChange(number + 1)}
                    >
                        {number + 1}
                    </Pagination.Item>
                ))}
            </Pagination>
        </Container>
    )
}

export default ProductPage;
