import React, { useState } from "react";
import { Form, Button, Tabs, Tab } from "react-bootstrap"; // React-Bootstrap  UI 
import { useNavigate } from "react-router-dom"; //    React Router 
import "./Login.css"; //   

function FindUser() {
    //      (: "findid" )
    const [key, setKey] = useState("findid");

    //     
    const [name, setName] = useState(""); //   
    const [phone, setPhone] = useState(""); //   
    const [email, setEmail] = useState(""); //   

    //    
    const [userId, setUserId] = useState(""); //      
    const [foundID, setFoundID] = useState(""); //     

    //    
    const [resetMessage, setResetMessage] = useState(""); //    

    //    (API   )
    const [isLoading, setIsLoading] = useState(false);

    // React Router    
    const navigate = useNavigate();

    //      (  )
    const validateInputs = (inputs) => {
        // , ,    
        if (!inputs.name.trim() || !inputs.phone.trim() || !inputs.email.trim()) {
            alert("  .");
            return false; //  
        }

        //    (  )
        if (!/^\S+@\S+\.\S+$/.test(inputs.email)) {
            alert("   .");
            return false; //  
        }

        //    (: 010-1234-5678)
        if (!/^\d{3}-\d{3,4}-\d{4}$/.test(inputs.phone)) {
            alert("  (: 010-1234-5678) .");
            return false; //  
        }

        return true; //   
    };

    //     
    const handleFindID = async (e) => {
        e.preventDefault(); //      

        //  
        if (!validateInputs({ name, phone, email })) return;

        // API   
        const data = { name, phone, email };

        setIsLoading(true); //   
        try {
            //  POST 
            const response = await fetch(`${process.env.REACT_APP_API_URL}/api/findid`, {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(data),
            });

            const result = await response.json(); //   JSON  

            if (result.success) {
                //      
                setFoundID(result.userId);
            } else {
                //         
                alert(result.message || "   .");
            }
        } catch (error) {
            //       
            console.error("   :", error);
            alert("     .");
        } finally {
            setIsLoading(false); //   
        }
    };

    //     
    const handleFindPW = async (e) => {
        e.preventDefault(); //      

        //  
        if (!validateInputs({ name, phone, email })) return;

        //    
        if (!userId.trim()) {
            alert(" .");
            return;
        }

        // API   
        const data = { userId, name, phone, email };

        setIsLoading(true); //   
        try {
            //  POST 
            const response = await fetch(`${process.env.REACT_APP_API_URL}/api/findpw`, {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(data),
            });

            const result = await response.json(); //   JSON  

            if (result.success) {
                //       
                setResetMessage("   .");
            } else {
                //         
                alert(result.message || "   .");
            }
        } catch (error) {
            //       
            console.error("   :", error);
            alert("     .");
        } finally {
            setIsLoading(false); //   
        }
    };

    return (
        <div className="login-container">
            {/*   */}
            <h5 className="font-label">/ </h5>
            <hr />

            {/* Tabs        */}
            <Tabs
                id="find-user-tabs"
                activeKey={key} //   
                onSelect={(k) => setKey(k)} //    key  
                className="mb-3"
            >
                {/*    */}
                <Tab eventKey="findid" title=" ">
                    <Form className="login-form" onSubmit={handleFindID}>
                        <Form.Group className="mb-3" controlId="formName">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="text"
                                placeholder=""
                                value={name}
                                onChange={(e) => setName(e.target.value)}
                            />
                        </Form.Group>

                        <Form.Group className="mb-3" controlId="formPhone">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="text"
                                placeholder="010-1234-5678"
                                value={phone}
                                onChange={(e) => setPhone(e.target.value)}
                            />
                        </Form.Group>

                        <Form.Group className="mb-3" controlId="formEmail">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="email"
                                placeholder="example@email.com"
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                            />
                        </Form.Group>

                        <Button
                            className="font-label btn-submit"
                            type="submit"
                            disabled={isLoading} //    
                        >
                            {isLoading ? " ..." : " "}
                        </Button>
                    </Form>
                    {/*     */}
                    {foundID && (
                        <div className="mt-3">
                            <p> : <strong>{foundID}</strong></p>
                            <Button variant="secondary" onClick={() => navigate("/login")}>
                                  
                            </Button>
                        </div>
                    )}
                </Tab>

                {/*    */}
                <Tab eventKey="findpw" title=" ">
                    <Form className="login-form" onSubmit={handleFindPW}>
                        <Form.Group className="mb-3" controlId="formUserId">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="text"
                                placeholder=""
                                value={userId}
                                onChange={(e) => setUserId(e.target.value)}
                            />
                        </Form.Group>

                        <Form.Group className="mb-3" controlId="formName">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="text"
                                placeholder=""
                                value={name}
                                onChange={(e) => setName(e.target.value)}
                            />
                        </Form.Group>

                        <Form.Group className="mb-3" controlId="formPhone">
                            <Form.Label></Form.Label>
                            <Form.Control
                                type="text"
                                placeholder="010-1234-5678"
                                value={phone}
                                onChange={(e) => setPhone(e.target.value)}
                            />
                        </Form.Group>

                        <Form.Group className="mb-3" controlId="formEmail">
                            <Form.Label
                            ></Form.Label>
                            <Form.Control
                                type="email"
                                placeholder="example@email.com"
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                            />
                        </Form.Group>

                        {/*    */}
                        <Button
                            className="font-label btn-submit"
                            type="submit"
                            disabled={isLoading} //    
                        >
                            {isLoading ? " ..." : " "}
                        </Button>
                    </Form>

                    {/*      */}
                    {resetMessage && (
                        <div className="mt-3">
                            <p>{resetMessage}</p>
                            <Button variant="secondary" onClick={() => navigate("/login")}>
                                  
                            </Button>
                        </div>
                    )}
                </Tab>
            </Tabs>
        </div>
    );
}

export default FindUser;
