import React from 'react';
import { Button, Col, Form, InputGroup, Row } from 'react-bootstrap';
import * as formik from 'formik';
import * as yup from 'yup';
import axios from 'axios';
import './Join.css';

function StoreJoin() {
    const {Formik} = formik;

    const schema = yup.object().shape({
        storeId: yup.string().required('  .'),
        password: yup.string().min(6, '  6  .').required(' .'),
        confirmPassword: yup.string().oneOf([yup.ref('password'), null], '  .').required('  .'),
        storeName: yup.string().required(' .'),
        phone: yup.string()
            .matches(/^(\d{3})-(\d{4})-(\d{4})$/, '   010-1234-5678.')
            .required(' .'),
        taxId: yup.string()
            .matches(/^(\d{3})-(\d{2})-(\d{5})$|^(\d{3})-(\d{2})-(\d{4})$/, '    000-00-0000  000-00-00000.')
            .required('  .'),
        email: yup.string().email('  .').required(' .'),
    });

    return (
        <div className="join-container">
            <Formik
                validationSchema={schema}
                onSubmit={async (values, {setSubmitting, resetForm}) => {
                    try {
                        const response = await axios.post('http://localhost:18080/api/storejoin', values, {
                            headers: {
                                'Content-Type': 'application/json'
                            }
                        });
                        console.log(' :', response.data);
                        alert(' .  2~3 .');
                        resetForm();
                        window.location.href = '/';
                    } catch (error) {
                        console.error(' :', error);
                        alert(' .   .');
                    }
                    setSubmitting(false);
                    console.log(" ", values);
                }}
                initialValues={{
                    storeId: '',
                    password: '',
                    confirmPassword: '',
                    storeName: '',
                    phone: '',
                    taxId: '',
                    taxFile: null,
                    email: '',
                    storeContact: ''
                }}
            >
                {({handleSubmit, handleChange, values, errors, isSubmitting}) => (
                    <Form noValidate onSubmit={handleSubmit} className="join-form">
                        <h5 className="font-label"></h5>
                        <hr/>
                        <Row className="form-group">
                            <Form.Group as={Col} md="12" controlId="validationFormikstoreId">
                                <Form.Label className="font-label"> </Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder=" "
                                    name="storeId"
                                    value={values.storeId}
                                    onChange={handleChange}
                                    isInvalid={!!errors.storeId}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.storeId}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="form-group">
                            <Form.Group as={Col} md="12" controlId="validationFormikPassword">
                                <Form.Label className="font-label"></Form.Label>
                                <Form.Control
                                    type="password"
                                    placeholder=""
                                    name="password"
                                    value={values.password}
                                    onChange={handleChange}
                                    isInvalid={!!errors.password}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.password}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="form-group">
                            <Form.Group as={Col} md="12" controlId="validationFormikConfirmPassword">
                                <Form.Label className="font-label"> </Form.Label>
                                <Form.Control
                                    type="password"
                                    placeholder=" "
                                    name="confirmPassword"
                                    value={values.confirmPassword}
                                    onChange={handleChange}
                                    isInvalid={!!errors.confirmPassword}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.confirmPassword}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="form-group">
                            <Form.Group as={Col} md="12" controlId="validationFormikStoreName">
                                <Form.Label className="font-label"> ()</Form.Label>
                                <Form.Control
                                    type="text"
                                    name="storeName"
                                    value={values.storeName}
                                    onChange={handleChange}
                                    isInvalid={!!errors.storeName}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.storeName}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="form-group">
                            <Form.Group as={Col} md="12" controlId="validationFormikPhone">
                                <Form.Label className="font-label"> </Form.Label>
                                <InputGroup>
                                    <Form.Control
                                        type="text"
                                        name="phone"
                                        placeholder="010-1234-5678"
                                        value={values.phone}
                                        onChange={handleChange}
                                        isInvalid={!!errors.phone}
                                    />
                                </InputGroup>
                                <Form.Control.Feedback type="invalid">{errors.phone}</Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="form-group">
                            <Form.Group as={Col} md="12" controlId="validationFormikTaxId">
                                <Form.Label className="font-label"> </Form.Label>
                                <InputGroup>
                                    <Form.Control
                                        type="text"
                                        placeholder="000-00-0000  000-00-00000"
                                        name="taxId"
                                        value={values.taxId}
                                        onChange={handleChange}
                                        isInvalid={!!errors.taxId}>

                                    </Form.Control>
                                </InputGroup>
                                <Form.Control.Feedback type="invalid">
                                    {errors.taxId}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="form-group">
                            <Form.Group as={Col} md="12" controlId="validationFormikFile">
                                <Form.Label className="font-label">  </Form.Label>
                                <Form.Control
                                    type="file"
                                    required
                                    name="taxFile"
                                    onChange={handleChange}
                                    isInvalid={!!errors.taxFile}
                                    className="file-input"
                                />
                            </Form.Group>
                        </Row>

                        <Row className="form-group">
                            <Form.Group as={Col} md="12" controlId="validationFormikEmail">
                                <Form.Label className="font-label"></Form.Label>
                                <Form.Control
                                    type="email"
                                    placeholder=""
                                    name="email"
                                    value={values.email}
                                    onChange={handleChange}
                                    isInvalid={!!errors.email}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.email}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="form-group">
                            <Form.Group as={Col} controlId="validationFormikContact">
                                <Form.Label className="font-label"> </Form.Label>
                                <Form.Control
                                    as="textarea"
                                    name="storeContact"
                                    value={values.storeContact}
                                    onChange={handleChange}
                                    isInvalid={!!errors.storeContact}
                                    className="join-form-textarea"
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.storeContact}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Button type="submit" className="btn-submit" disabled={isSubmitting}>
                            {isSubmitting ? ' ...' : ''}
                        </Button>
                    </Form>
                )}
            </Formik>
        </div>
    );
}

export default StoreJoin;
