import React, {useState, useEffect} from 'react';
import { Button, Col, Form, InputGroup, Row } from 'react-bootstrap';
import * as formik from 'formik';
import * as yup from 'yup';
import axios from 'axios';
import "./Join.css";

function Join() {
    const {Formik} = formik;

    const [zipcode, setZipcode] = useState('');
    const [address, setAddress] = useState('');
    const [detailAddress, setDetailAddress] = useState('');

    useEffect(() => {
        if (window.daum) return;

        const script = document.createElement('script');
        script.src = 'https://t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js';
        script.onload = () => {
            console.log('Daum Postcode script loaded');
        };
        document.body.appendChild(script);
    }, []);

    const schema = yup.object().shape({
        userId: yup.string().required(' .'),
        password: yup.string().min(6, '  6  .').required(' .'),
        confirmPassword: yup.string().oneOf([yup.ref('password'), null], '  .').required('  .'),
        userName: yup.string().required(' .'),
        phone: yup.string()
            .matches(/^(\d{3})-(\d{4})-(\d{4})$/, '   010-1234-5678.')
            .required(' .'),
        zip: yup.string().required(' .'),
        address: yup.string().required(' .'),
        detailAddress: yup.string().required(' .'),
        email: yup.string().email('  .').required(' .'),
        receiveEmail: yup.string().oneOf(['Y', 'N'], '  .')
    });

    const handleSearchPostcode = (setFieldValue) => {
        new window.daum.Postcode({
            oncomplete: function (data) {
                setZipcode(data.zonecode);
                setAddress(data.roadAddress);
                setFieldValue('zip', data.zonecode);
                setFieldValue('address', data.roadAddress);
            }
        }).open();
    };

    return (
        <div className="join-container">
            <Formik
                validationSchema={schema}
                onSubmit={async (values, {setSubmitting, resetForm}) => {
                    console.log('  :', values);
                    try {
                        const response = await axios.post('http://localhost:18080/api/join', values, {
                            headers: {
                                'Content-Type': 'application/json'
                            }
                        });
                        console.log(' :', response.data);
                        alert(' .');
                        resetForm();
                        window.location.href = '/';
                    } catch (error) {
                        console.error(' :', error);
                        alert(' .   .');
                    }
                    setSubmitting(false);
                    console.log(" ", values);
                }}
                initialValues={{
                    userId: '',
                    password: '',
                    confirmPassword: '',
                    userName: '',
                    phone: '',
                    zip: '',
                    address: '',
                    detailAddress: '',
                    email: '',
                    receiveEmail: 'N',
                    auth: 'user'
                }}
            >
                {({handleSubmit, handleChange, setFieldValue, values, touched, errors, isSubmitting}) => (
                    <Form noValidate onSubmit={handleSubmit} className="join-form">
                        <h5 className="font-label"></h5>
                        <hr/>
                        <Row className="mb-3">
                            <Form.Group as={Col} md="12" controlId="validationFormikUserId">
                                <Form.Label className="font-label"></Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder=""
                                    name="userId"
                                    value={values.userId}
                                    onChange={handleChange}
                                    isInvalid={!!errors.userId}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.userId}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="mb-3">
                            <Form.Group as={Col} md="12" controlId="validationFormikPassword">
                                <Form.Label className="font-label"></Form.Label>
                                <Form.Control
                                    type="password"
                                    placeholder=""
                                    name="password"
                                    value={values.password}
                                    onChange={handleChange}
                                    isInvalid={!!errors.password}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.password}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="mb-3">
                            <Form.Group as={Col} md="12" controlId="validationFormikConfirmPassword">
                                <Form.Label className="font-label"> </Form.Label>
                                <Form.Control
                                    type="password"
                                    placeholder=" "
                                    name="confirmPassword"
                                    value={values.confirmPassword}
                                    onChange={handleChange}
                                    isInvalid={!!errors.confirmPassword}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.confirmPassword}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="mb-3">
                            <Form.Group as={Col} md="12" controlId="validationFormikUserName">
                                <Form.Label className="font-label"></Form.Label>
                                <Form.Control
                                    type="text"
                                    name="userName"
                                    value={values.userName}
                                    onChange={handleChange}
                                    isInvalid={!!errors.userName}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.userName}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="mb-3">
                            <Form.Group as={Col} md="12" controlId="validationFormikPhone">
                                <Form.Label className="font-label"></Form.Label>
                                <InputGroup>
                                    <Form.Control
                                        type="text"
                                        placeholder="010-1234-5678"
                                        name="phone"
                                        value={values.phone}
                                        onChange={handleChange}
                                        isInvalid={!!errors.phone}>

                                    </Form.Control>
                                </InputGroup>
                                <Form.Control.Feedback type="invalid">
                                    {errors.phone}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="mb-3">
                            <Form.Group as={Col} md="12" controlId="validationFormikZip">
                                <Form.Label className="font-label"></Form.Label>
                                <InputGroup>
                                    <Form.Control
                                        type="text"
                                        placeholder=""
                                        name="zip"
                                        value={zipcode || values.zip}
                                        onChange={handleChange}
                                        isInvalid={!!errors.zip}
                                        readOnly
                                    />
                                    <Button className="font-label" onClick={() => handleSearchPostcode(setFieldValue)}> </Button>
                                </InputGroup>
                                <Form.Control.Feedback type="invalid">
                                    {errors.zip}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="mb-3">
                            <Form.Group as={Col} md="12" controlId="validationFormikAddress">
                                <Form.Label className="font-label"></Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder=" "
                                    name="address"
                                    value={address || values.address}
                                    onChange={handleChange}
                                    isInvalid={!!errors.address}
                                    readOnly
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.address}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="mb-3">
                            <Form.Group as={Col} md="12" controlId="validationFormikDetailAddress">
                                <Form.Label className="font-label"></Form.Label>
                                <Form.Control
                                    type="text"
                                    placeholder=""
                                    name="detailAddress"
                                    value={detailAddress || values.detailAddress}
                                    onChange={handleChange}
                                    isInvalid={!!errors.detailAddress}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.detailAddress}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="mb-3">
                            <Form.Group as={Col} md="12" controlId="validationFormikEmail">
                                <Form.Label className="font-label"></Form.Label>
                                <Form.Control
                                    type="email"
                                    placeholder=""
                                    name="email"
                                    value={values.email}
                                    onChange={handleChange}
                                    isInvalid={!!errors.email}
                                />
                                <Form.Control.Feedback type="invalid">
                                    {errors.email}
                                </Form.Control.Feedback>
                            </Form.Group>
                        </Row>

                        <Row className="mb-3">
                            <Form.Group as={Col} md="6" controlId="validationFormikReceiveEmail">
                                <Form.Check className="font-label"
                                    type="checkbox"
                                    name="receiveEmail"
                                    label="  "
                                    checked={values.receiveEmail === 'Y'}
                                    onChange={() => {
                                        setFieldValue('receiveEmail', values.receiveEmail === 'Y' ? 'N' : 'Y');
                                    }}
                                />
                            </Form.Group>
                        </Row>

                        <Button className="btn-submit" type="submit" disabled={isSubmitting}>
                            {isSubmitting ? " ..." : ""}
                        </Button>

                    </Form>
                )}
            </Formik>
        </div>
    );
}

export default Join;
