import React, { useState } from "react";
import DaumPostcode from "react-daum-postcode"; // DaumPostcode 
import "./DeliveryMod.css";

//   
const formatPhoneNumber = (phone) => {
  if (!phone) return "";
  const cleaned = phone.replace(/[^0-9]/g, ""); //    
  const match = cleaned.match(/^(\d{3})(\d{4})(\d{4})$/);
  if (match) {
    return `${match[1]}-${match[2]}-${match[3]}`;
  }
  return phone;
};

function DeliveryPopup({ onAdd, onClose }) {
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [address, setAddress] = useState("");
  const [postcode, setPostcode] = useState("");
  const [detailAddress, setDetailAddress] = useState(""); //   
  const [isAddressModalOpen, setIsAddressModalOpen] = useState(false); //   

  // DaumPostcode  
  const handleCompleteAddress = (data) => {
    const fullAddress = data.roadAddress || data.jibunAddress;
    setAddress(fullAddress);
    setPostcode(data.zonecode); //  
    setIsAddressModalOpen(false); //  
  };

  //    ( , 11 )
  const handlePhoneChange = (e) => {
    const value = e.target.value.replace(/[^0-9]/g, ""); //    
    if (value.length <= 11) {
      setPhone(value);
    }
  };

  //    
  const handleAdd = () => {
    if (!name || !phone || !address || !postcode || !detailAddress) {
      alert("   .");
      return;
    }

    const newAddress = {
      name,
      phone: formatPhoneNumber(phone), //   
      address,
      postcode,
      detailAddress, //  
    };
    onAdd(newAddress); //   
    onClose(); //  
  };

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <h2 className="modal-title"> </h2>

        {/*  */}
        <div className="form-group">
          <label></label>
          <input
            type="text"
            value={name}
            onChange={(e) => setName(e.target.value)}
            className="form-input"
          />
        </div>

        {/*  */}
        <div className="form-group">
          <label></label>
          <input
            type="text"
            value={phone}
            onChange={handlePhoneChange}
            className="form-input"
            placeholder="  (11)"
          />
        </div>

        {/*  */}
        <div className="form-group">
          <label></label>
          <div className="address-wrapper">
            <input
              type="text"
              value={address}
              readOnly
              className="form-input"
            />
            <button
              onClick={() => setIsAddressModalOpen(true)}
              className="btn-address-search"
            >
              
            </button>
          </div>
        </div>

        {/*  */}
        <div className="form-group">
          <label></label>
          <input
            type="text"
            value={postcode}
            readOnly
            className="form-input"
          />
        </div>

        {/*  */}
        <div className="form-group">
          <label></label>
          <input
            type="text"
            value={detailAddress}
            onChange={(e) => setDetailAddress(e.target.value)}
            className="form-input"
            placeholder=" "
          />
        </div>

        {/*     */}
        <div className="button-group">
          <button onClick={onClose} className="btn-cancel">
            
          </button>
          <button onClick={handleAdd} className="btn-submit">
            
          </button>
        </div>

        {/* DaumPostcode  */}
        {isAddressModalOpen && (
          <div className="modal-overlay">
            <div className="address-modal">
              <DaumPostcode onComplete={handleCompleteAddress} />
              <button
                onClick={() => setIsAddressModalOpen(false)}
                className="btn-close-modal"
              >
                
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default DeliveryPopup;