import React, { useState } from "react";

function ProductDetail() {
  const [selectedOption, setSelectedOption] = useState(""); //   
  const [additionalOption, setAdditionalOption] = useState(""); //   
  const [quantity, setQuantity] = useState(1); //  
  const [price, setPrice] = useState(17900); //  

  const handleOptionChange = (e) => {
    setSelectedOption(e.target.value);
  };

  const handleAdditionalOptionChange = (e) => {
    setAdditionalOption(e.target.value);
  };

  const handleQuantityChange = (type) => {
    if (type === "increase") {
      setQuantity((prev) => prev + 1);
    } else if (type === "decrease" && quantity > 1) {
      setQuantity((prev) => prev - 1);
    }
  };

  return (
    <div style={{ display: "flex", padding: "20px", fontFamily: "Arial, sans-serif" }}>
      {/*    */}
      <div style={{ flex: 1, paddingRight: "20px" }}>
        <img
          src="https://via.placeholder.com/400"
          alt="Product"
          style={{ width: "100%", borderRadius: "8px" }}
        />
        {/*   */}
        <div style={{ display: "flex", marginTop: "10px", gap: "10px" }}>
          {[1, 2, 3, 4].map((item) => (
            <img
              key={item}
              src={`https://via.placeholder.com/100?text=Img${item}`}
              alt={`Thumbnail ${item}`}
              style={{ width: "80px", height: "80px", borderRadius: "8px", cursor: "pointer" }}
            />
          ))}
        </div>
      </div>

      {/*     */}
      <div style={{ flex: 1.5 }}>
        <h2 style={{ fontSize: "24px", fontWeight: "bold", marginBottom: "10px" }}>
          New Color    3 
        </h2>
        <p style={{ fontSize: "14px", color: "#888", marginBottom: "10px" }}>
           | 
        </p>
        <p style={{ fontSize: "20px", fontWeight: "bold", color: "#FF5722", marginBottom: "20px" }}>
          17,900
        </p>

        {/*   */}
        <div style={{ marginBottom: "20px", fontSize: "14px", lineHeight: "1.5" }}>
          <p>:  10%  ()</p>
          <p>:  |     </p>
        </div>

        {/*   */}
        <div style={{ marginBottom: "20px" }}>
          <label style={{ fontWeight: "bold", display: "block", marginBottom: "5px" }}>
            
          </label>
          <select
            value={selectedOption}
            onChange={handleOptionChange}
            style={{
              width: "100%",
              padding: "10px",
              border: "1px solid #ddd",
              borderRadius: "4px",
              fontSize: "14px",
            }}
          >
            <option value=""> </option>
            <option value="option1"> 1</option>
            <option value="option2"> 2</option>
          </select>
        </div>

        {/*    */}
        <div style={{ marginBottom: "20px" }}>
          <label style={{ fontWeight: "bold", display: "block", marginBottom: "5px" }}>
             ()
          </label>
          <select
            value={additionalOption}
            onChange={handleAdditionalOptionChange}
            style={{
              width: "100%",
              padding: "10px",
              border: "1px solid #ddd",
              borderRadius: "4px",
              fontSize: "14px",
            }}
          >
            <option value=""> </option>
            <option value="additional1"> 1</option>
            <option value="additional2"> 2</option>
          </select>
        </div>

        {/*   */}
        <div style={{ display: "flex", alignItems: "center", marginBottom: "20px" }}>
          <button
            onClick={() => handleQuantityChange("decrease")}
            style={{
              padding: "10px",
              backgroundColor: "#ddd",
              border: "none",
              borderRadius: "4px",
              cursor: "pointer",
              fontSize: "16px",
            }}
          >
            -
          </button>
          <span style={{ margin: "0 10px", fontSize: "16px" }}>{quantity}</span>
          <button
            onClick={() => handleQuantityChange("increase")}
            style={{
              padding: "10px",
              backgroundColor: "#ddd",
              border: "none",
              borderRadius: "4px",
              cursor: "pointer",
              fontSize: "16px",
            }}
          >
            +
          </button>
        </div>

        {/*   */}
        <div style={{ fontSize: "16px", fontWeight: "bold", marginBottom: "20px" }}>
           : {(price * quantity).toLocaleString()}
        </div>

        {/*  */}
        <div style={{ display: "flex", gap: "10px" }}>
          <button
            style={{
              flex: 1,
              padding: "15px",
              backgroundColor: "#FFD700",
              border: "none",
              borderRadius: "4px",
              fontSize: "16px",
              cursor: "pointer",
            }}
          >
            
          </button>
          <button
            style={{
              flex: 1,
              padding: "15px",
              backgroundColor: "#FF5722",
              color: "white",
              border: "none",
              borderRadius: "4px",
              fontSize: "16px",
              cursor: "pointer",
            }}
          >
            
          </button>
        </div>
      </div>
    </div>
  );
}

export default ProductDetail;
